<template>
    <div class="header">
		<div class="content-wrapper">
			<div class="avatar">
				<img :src="seller.avatar" alt="" width="64" height="64">
			</div>
			<div class="content">
				<div class="title">
					<span class="brand"></span>
					<span class="name">{{seller.name}}</span>
				</div>
				<div class="description">
					{{seller.description}}/{{seller.deliveryTime}}分钟送达
				</div>
				<div class="support" v-if="seller.supports">
					<span class="icon" :class="classMap[seller.supports[0].type]"></span>
					<span class="text">{{seller.supports[0].description}}</span>
				</div>
			</div>
			<div v-if="seller.supports" @click="showDetail" class="support-count">
				<span class="count">{{seller.supports.length}}个</span>
				<i class="iconfont icon-right"></i>
			</div>
		</div>
		<div class="bulletin-wrapper" @click="showDetail">
			<span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
			<i class="iconfont icon-right"></i>
		</div>
		<div class="background">
			<img :src="seller.avatar" width="100%" height="100%">
		</div>
		<transition name="fade">
			<div v-show="detailShow" class="detail">
				<div class="detail-wrapper clearfix">
					<div class="detail-main">
						<h1 class="name">{{seller.name}}</h1>
						<div class="star-wrapper">
							<star :size="48" :score="4.5"></star>
						</div>
						<div class="title">
							<div class="line"></div>
							<div class="text">优惠信息</div>
							<div class="line"></div>
						</div>
						<ul v-if="seller.supports" class="supports">
							<li class="support-item" v-for="(item, index) in seller.supports" :key="index">
								<span class="icon" :class="classMap[seller.supports[index].type]"></span>
								<span class="text">{{seller.supports[index].description}}</span>
							</li>
						</ul>
						<div class="title">
							<div class="line"></div>
							<div class="text">商家公告</div>
							<div class="line"></div>
						</div>
						<div class="bulletin">
							<p class="content">{{seller.bulletin}}</p>
						</div>
					</div>
				</div>
				<div class="detail-close" @click="hideDetail">
					<i class="iconfont icon-close"></i>
				</div>
			</div>
		</transition>
    </div>
</template>

<script>
import star from '../star/star.vue'
export default {
	props: {
		seller: {
			type: Object
		}
	},
	data(){
		return {
			detailShow: false
		}
	},
	methods: {
		showDetail(){
			this.detailShow = true;
		},
		hideDetail(){
			this.detailShow = false;
		}
	},
	created(){
		this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
	},
	components: {
		"star": star
	}
}
</script>

<style lang="less">
 
.header{
	position: relative;
	color: #fff;
	background-color: rgba(7, 17, 27, 0.5);
	overflow: hidden;
	.content-wrapper{
		position: relative;
		padding: 24px 12px 18px 24px;
		font-size: 0;	/*并列元素有换行空格产生的空隙，父元素fz=0，子元素要设fz*/
		.avatar{
			display: inline-block;
			vertical-align: top;
			img{
				border-radius: 2px;
			}
		}
		.content{
			display: inline-block;
			margin-left: 16px;
			.title{
				margin: 2px 0 8px 0;
				.brand{
					width: 30px;
					height: 18px;
					display: inline-block;
					background: url(brand.png) no-repeat;
					background-size: 30px 18px;
					vertical-align: top;
					border-radius: 2px;
				}
				.name{
					margin-left: 6px;
					font-size: 16px;
					line-height: 18px;
					font-weight: bold;
				}
			}
			.description{
				margin-bottom: 10px;
				line-height: 12px;
				font-size: 12px;
			}
			.support{
				.icon{
					display: inline-block;
					width: 12px;
					height: 12px;
					margin-right: 4px;
					background-size: 12px 12px;
					background-repeat: no-repeat;
					vertical-align: top;
					&.decrease{
						background-image: url(decrease.png);
					}
					&.discount{
						background-image: url(discount.png);
					}
					&.special{
						background-image: url(special.png);
					}
					&.invoice{
						background-image: url(invoice.png);
					}
					&.guarantee{
						background-image: url(guarantee.png);
					}
				}
				.text{
					line-height: 12px;
					font-size: 10px;
				}
			}
		}
		.support-count{
			position: absolute;
			right: 12px;
			bottom: 18px;
			padding: 0 8px;
			height: 24px;
			line-height: 24px;
			border-radius: 14px;
			background-color: rgba(0, 0, 0, 0.2);
			text-align: center;
			.count{
				font-size: 10px;
				vertical-align: top;
			}
			.icon-right{
				font-size: 10px;
				margin-left: 2px;
			}
		}
	}
	.bulletin-wrapper{
		position: relative;
		background-color: rgba(7, 17, 24, 0.2);
		height: 28px;
		line-height: 28px;
		padding: 0 22px 0 12px; 
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		.bulletin-title{
			display: inline-block;
			width: 22px;
			height: 12px;
			background: url(bulletin.png) no-repeat;
			background-size: 22px 12px;
		}
		.bulletin-text{
			vertical-align: top;
			margin: 0 4px;
			font-size: 10px;
		}
		.icon-right{
			position: absolute;
			right: 12px;
			top: 0px;
			font-size: 10px;
		}
	}
	.background{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
		filter: blur(8px);
		overflow: hidden;
	}
	.detail{
		position: fixed;
		top: 0;
		left: 0;
		z-index: 100;
		width: 100%;
		height: 100%;
		overflow: auto;
		-webkit-backdrop-filter: blur(10px);
		background: rgba(7, 17, 27, 0.8);
		opacity: 1;
		&.fade-enter-active,&.fade-leave-active{
			transition: all .3s ease;
		}
		&.fade-enter{
			transform: translateY(-100%);
		}
		&.fade-leave-to{
			opacity: 0;
			background: rgba(7, 17, 27, 0);
		}
		.detail-wrapper{
			min-height: 100%;
			width: 100%;
			.detail-main{
				margin-top: 64px;
				padding-bottom: 64px; 
				.name{
					text-align: center;
					line-height: 16px;
					font-size: 16px;
					font-weight: 700;
				}
				.star-wrapper{
					margin-top: 18px;
					padding: 2px 0;
					text-align: center;
				}
				.title{
					display: flex;
					width: 80%;
					margin: 28px auto 24px auto;
					.line{
						flex: 1;
						position: relative;
						top: -6px;
						border-bottom: 1px solid rgba(255, 255, 255, 0.2);
					}
					.text{
						padding: 0 12px;
						font-size: 14px;
						font-weight: 700;
					}
				}
				.supports{
					width: 80%;
					margin: 0 auto;
					.support-item{
						padding: 0 12px;
						margin-bottom: 12px;
						font-size: 0;
						&:last-child{
							margin-bottom: 0;
						}
						.icon{
							display: inline-block;
							width: 16px;
							height: 16px;
							vertical-align: top;
							margin-right: 6px;
							background-size: 16px 16px;
							background-repeat: no-repeat;
							&.decrease{
								background-image: url(decrease.png);
							}
							&.discount{
								background-image: url(discount.png);
							}
							&.special{
								background-image: url(special.png);
							}
							&.invoice{
								background-image: url(invoice.png);
							}
							&.guarantee{
								background-image: url(guarantee.png);
							}
						}
						.text{
							line-height: 16px;
							font-size: 12px;
						}
					}
				}
				.bulletin{
					width: 80%;
					margin: 0 auto;
					.content{
						padding: 0 12px;
						font-size: 12px;
						line-height: 24px;
					}
				}
			}
		}
		.detail-close{
			position: relative;
			width: 32px;
			height: 32px;
			margin: -64px auto;
			clear: both;
			.icon-close{
				font-size: 30px;
			}
		}
	}
}
</style>


