<template>
    <transition name="move">
        <div class="food" v-show="showFlag" ref="food">
            
            <div class="food-content">
                <div class="img-header">
                    <img :src="food.image">
                </div>
                <div class="content">
                    <h1 class="title">{{food.name}}</h1>
                    <div class="detail">
                        <span class="sell-count">月售{{food.sellCount}}份</span>
                        <span class="rating">好评率{{food.rating}}%</span>
                    </div>
                    <div class="price">
                        <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                    </div>
                    <div class="cartcontrol-wrapper">
                        <cartcontrol @add="addFood" :food="food"></cartcontrol>
                    </div>
                    <transition name="fade">
                        <div class="buy" @click.stop.prevent="addFirst" v-show="!food.count || food.count ===0">加入购物车</div>
                    </transition>
                </div>
                <split v-show="food.info"></split>
                <div class="info" v-show="food.info">
                    <h1 class="title">商品信息</h1>
                    <p class="text">{{food.info}}</p>
                </div>
                <split></split>
                <div class="rating">
                    <h1 class="title">商品评价</h1>
                    <ratingselect @select="selectRating" @toggle="toggleContent" :selectType="selectType" :onlyContent="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>                    
                    <div class="rating-wrapper">
                        <ul v-show="food.ratings && food.ratings.length">
                            <li v-show="needShow(rating.rateType, rating.text)" v-for="(rating, index) in food.ratings" :key="index" class="rating-item">
                                <div class="user">
                                    <span class="name">{{rating.username}}</span>
                                    <img class="avatar" width="12" height="12" :src="rating.avatar">
                                </div>
                                <div class="time">{{rating.rateTime | formatDate}}</div>
                                <p class="text">
                                    <i class="iconfont" :class="{'icon-thumb_up': rating.rateType===0,'icon-thumbdown': rating.rateType===1}"></i>{{rating.text}}
                                </p>
                            </li>
                        </ul>
                        <div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
                    </div>
                </div>
            </div>
            <div class="back">
                <i class="iconfont icon-back" @click.stop.prevent="hide"></i>
            </div>
        </div>
    </transition>
</template>

<script>

import BScroll from 'better-scroll'
import cartcontrol from 'components/cartcontrol/cartcontrol.vue'
import ratingselect from 'components/ratingselect/ratingselect.vue'
import split from 'components/split/split.vue'
import Vue from 'vue'
import {formatDate} from 'common/js/date.js'

const POSITIVE = 0
const NEGATIVE = 1
const ALL = 2

export default {
    props: {
        food: {
            type: Object
        }
    },
    data(){
        return {
            showFlag: false,
            selectType: ALL,
            onlyContent: true,
            desc: {
                all: '全部',
                positive: '推荐',
                negative: '吐槽'
            }
        }
    },
    created(){
        
    },
    methods: {
        show(){
            this.showFlag = true
            this.selectType = ALL
            this.onlyContent = true
            this.$nextTick(() => {
                if(!this.scroll){
                    this.scroll = new BScroll(this.$refs.food, {
                        click: true
                    })
                }else{
                    this.scroll.refresh()
                }
            })
        },
        hide(){
            this.showFlag = false
        },
        addFirst(event){
            
            if(!event._constructed){
                return
            }
            this.$emit('add', event.target)
            Vue.set(this.food, 'count', 1)
        },
        addFood(target){
            this.$emit('add', target)
        },
        needShow(type, text){
            if(this.onlyContent && !text){
                return false
            }
            if(this.selectType === ALL){
                return true
            }else{
                return type === this.selectType
            }
        },
        selectRating(type){
            this.selectType = type
            this.$nextTick(() => {
                this.scroll.refresh()
            })
        },
        toggleContent(){
            this.onlyContent = !this.onlyContent
            this.$nextTick(() => {
                this.scroll.refresh()
            })
        },
    },
    filters: {
        formatDate(time){
            let date  = new Date(time)
            return formatDate(date, 'yyyy-MM-dd hh:mm')
        }
    },
    components: {
        cartcontrol,
        ratingselect,
        split
    }
}
</script>
<style lang="less">
.food{
    position: fixed;
    top: 0;
    bottom: 48px;
    left: 0;
    width: 100%;
    background-color: #fff;
    transform: translateX(0);
    opacity: 1;
    &.move-enter-active, &.move-leave-active{
        transition: all .3s ease;
        opacity: 1;
    }
    &.move-enter, &.move-leave-to{
        opacity: 0;
        transform: translateX(100%);
    }
    .back{
        position: fixed;
        top: 10px;
        left: 10px;
        background-color: rgba(7,17,27,0.4);
        border-radius: 50%;
        .icon-back{
            display: inline-block;
            padding: 6px;
            color: #fff;
            font-size: 26px;
            
        }
    }
    
    // 异步加载图片，固定大小
    .img-header{
        position: relative;
        width: 100%;
        height: 0;
        padding-top: 100%;  // 按width比例
        img{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
        
    }
    .content{
        position: relative;
        padding: 18px;
        .title{
            font-size: 14px;
            line-height: 14px;
            margin-bottom: 8px;
            font-weight: 700;
            color: rbg(7,17,27);
        }
        .detail{
            font-size: 0;
            line-height: 10px;
            margin-bottom: 18px;
            height: 10px;
            .sell-count, .rating{
                font-size: 10px;
                color: rgb(147,153,159);
            }
            .sell-count{
                margin-right: 12px;
            }
        }
        .price{
            font-weight: 700;
            line-height: 24px;
            .now{
                margin-right: 8px;
                font-size: 14px;
                color: rgb(240, 20, 20);
            }
            .old{
                text-decoration: line-through;
                font-size: 10px;
                color: rgb(147, 153, 159);
            }
        }
        .cartcontrol-wrapper{
        position: absolute;
        right: 12px;
        bottom: 12px;
        }
        .buy{
            position: absolute;
            right: 18px;
            bottom: 18px;
            z-index: 10;
            height: 24px;
            line-height: 24px;
            padding: 0 12px;
            box-sizing: border-box;
            font-size: 10px;
            border-radius: 12px;
            color: #fff;
            background-color: rgb(0, 160, 220);
            &.fade-enter-actice, &.fade-leave-active{
                transition: all 0.2s;
                opacity: 1;
            }
            &.fade-enter, &.fade-leave-to{
                opacity: 0;
            }
        }
    }
    .info{
        padding: 18px;
        .title{
            font-size: 14px;
            margin-bottom: 6px;
            line-height: 14px;
            color: rgb(7,17,27);
        }
        .text{
            font-size: 12px;
            line-height: 24px;
            padding: 0 8px;
            color: rgb(77, 85, 93);
        }
    }
    .rating{
        padding-top: 18px;
        .title{
            font-size: 14px;
            margin-left: 18px;
            line-height: 14px;
            color: rgb(7,17,27);
        }
        .rating-wrapper{
            padding: 0 18px;
            .rating-item{
                position: relative;
                padding: 16px 0;
                border-bottom: 0.5px solid rgba(7,17,27,0.1);
                .user{
                    position: absolute;
                    right: 0;
                    top: 16px;
                    font-size: 0;
                    line-height: 12px;
                    .name{
                        display: inline-block;
                        margin-right: 6px;
                        vertical-align: top;
                        font-size: 10px;
                        color: rgb(147,153,159);
                    }
                    .avatar{
                        border-radius: 50%;
                    }
                }
                .time{
                    margin-bottom: 6px;
                    line-height: 12px;
                    font-size: 10px;
                    color: rgb(147,153,159);
                }
                .text{
                    font-size: 12px;
                    line-height: 16px;
                    color: rgb(7,17,27);
                    .icon-thumbdown, .icon-thumb_up{
                        margin-right: 4px;
                        line-height: 16px;
                        font-size: 12px;
                    }
                    .icon-thumb_up{
                        color: rgb(0, 160, 220);
                    }
                    .icon-thumbdown{
                        color: rgb(147,153,159);
                    }
                }
            }
            .no-rating{
                padding: 16px 0;
                font-size: 12px;
                color: rgb(147,153,159);
            }
        }
    }
    
}
</style>



